import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, Panel, PanelHeader, Select, Textarea, Checkbox, Button, Link, Radio, FormLayout, FormLayoutGroup, Input } from '@vkontakte/vkui';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';


class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      purpose: ''
    }

    this.onChange = this.onChange.bind(this);
  }

  onChange(e) {
    const { name, value } = e.currentTarget;
    this.setState({ [name]: value });
  }

  render() {
    return (
      <View activePanel="new-user">
        <Panel id="new-user">
          <PanelHeader>Vk-App</PanelHeader>
          <FormLayout>

            <Input type="text" top="Название рассматриваемой группы"/>
						<Input type="text" top="Название вашей группы"/>

						<Button type="submit" size="xl">Забанить пересечение</Button>

          </FormLayout>
        </Panel>
      </View>
    );
  }
}

export default App;
